.form {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.form input[type="text"] {
    height: 40px;
    padding: 0 20px;
    font-size: 20px;
    border: 1px solid #ccc;
    min-width: 80%;
}

.form button[type="submit"]{
    border: none;
    height: 40px;
    width: 40px;
    background-color: #ec755a;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}